import React from "react"
import {Col,Modal,Button} from 'react-bootstrap';
import {useState} from 'react';

const Sale = (props) => {
    const {sale} = props
    let [isHover, setIsHover] = useState(false);

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <Col md={3}>
            <img src={process.env.PUBLIC_URL +sale.imgUrl} width='100%' alt='sale' />
            <h5>{sale.title}</h5>
            <p>{"" + sale.price.toLocaleString()}</p>
            <p>{sale.discount}</p>
            <div className='heart' onClick={handleShow}></div>
            
        <Modal show={show} onHide={handleClose} className="interpro">
        <Modal.Header>
        <Modal.Title>  .
        <Button variant="dark" onClick={handleClose}>
            X
        </Button> 
        </Modal.Title>
        </Modal.Header>
        </Modal>  
        </Col>
    )
}
export default Sale