import {configureStore, createSlice} from '@reduxjs/toolkit'

let user = createSlice({
    name: 'user',
    initialState : {name: 'kim', age: 20},
    reducers: {
        changeName(state){
            state.name = 'john kim'
        }
    }
})

let cart = createSlice({
    name : 'cart',
    initialState : [
    
    ],
 
    reducers : {
       addCount(state, action){
          let idx = state.findIndex((a)=>{ return a.id === action.payload }) 
           ++state[idx].amount;
           if(state[idx].amount>20){
            alert('   20 .');
            state[idx].amount = 20;
            
           }
          
       },
       addCount2(state, action){
        let idx = state.findIndex((a)=>{ return a.id === action.payload }) 
        --state[idx].amount; 
        if(state[idx].amount<1){
            alert('   1 .');
            state[idx].amount = 1;
           }
         },

       sortName(state) {
          state.sort((a, b) => 
             a.item > b.item ? 1 : -1,
          );
       },
       
       addItem(state, action){
        let num = state.findIndex(a => a.id == action.payload.id)
        let stateId = action.payload.id
        if(num >= 0){
            if(action.payload.id === state[num].id){
                state[num].amount++
            }
        }else{
           state.push(action.payload) 
        }
       },

       price1(state) {
        state.sort((a,b)=> 
        a.price > b.price ? 1 : -1,
        );
       },

       deleteItem(state, action){
        let num = state.findIndex(a => a.id == action.payload)
        state.splice(num,1)
       }
    }

 })

 let quantity = createSlice({
    name : 'quantity',
    initialState : {pronum: 1},

    reducers : {
        increace(state, action){
            state.pronum += action.payload
            if(state.pronum > 20){
                alert('   20 .');
                state.pronum = 1;
               }
            
        },
        decrease(state, action){
            state.pronum -= action.payload
            if(state.pronum <1){
                alert('   1 .');
                state.pronum = 1;
               }
        
             }
    }
 })


 
export let { addCount, sortName, addItem, addCount2, price1, deleteItem } = cart.actions
export let {changeName} = user.actions
export let {decrease, increace} = quantity.actions

export default configureStore({
    reducer:{
        user:user.reducer,
        cart: cart.reducer,
        quantity: quantity.reducer
    }
})