import React from 'react'
import {Col, Nav} from 'react-bootstrap'
import {useNavigate } from 'react-router-dom';
import {useState} from 'react'

const Product = (props)=> {
    let navigate = useNavigate();
    let {gnews, i, no} = props;
    let [isHover, setIsHover] = useState(false);

    return(
        <Col md={3}>
            <Nav.Link onClick={() =>
                navigate('/detail/' + no[i])} onMouseOver={()=> {setIsHover(true)}} onMouseOut={()=> {setIsHover(false)}} >
                <img src={process.env.PUBLIC_URL + (isHover ? gnews.hoverImg: gnews.imgUrl)} width="100%" />
                <h5 >{gnews.title}</h5>
                <p>{"" + gnews.price.toLocaleString()}</p>
                <div className='heart'></div>
            </Nav.Link>
        </Col>
    )
}

export default Product