import React from "react";
import { Row, Col, Button, Nav, Table, Modal, Alert } from 'react-bootstrap'
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';
import { useState , useRef} from 'react';
import {Context1} from '../App';
import { useParams ,useNavigate,} from"react-router-dom";
import TabContainer from "react-bootstrap";
import Accordion from 'react-bootstrap/Accordion';
import { addItem, increace, decrease} from "../store";
import { useDispatch, useSelector } from "react-redux";
import Carousel from 'react-bootstrap/Carousel';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from 'swiper/modules';


import 'swiper/css';
import 'swiper/css/pagination';

const Detail = (props) => {
    let {id} = useParams();
    let {gnews} = props;
    const [radioValue, setRadioValue] = useState('1');

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let navigate = useNavigate();

    const radios = [
    { name: 'S', value: 'S' },
    { name: 'M', value: 'M' },
    { name: 'L', value: 'L' },
    { name: 'XL', value: 'XL' },
    { name: 'XXL', value: 'XXL' }
  ];

    let [tab, setTab] = useState(0);
    let selproduct = gnews.find((x) => x.id==id);
    
    let quantity = useSelector (state => state.quantity);
    let {pronum} = quantity;

    const dispatch = useDispatch();

    

  return (
    <div className="detailbox">
        <Row>
            
        <Col md={7}>

        <div className="firstbox">
        
        <Swiper
        spaceBetween={70}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination]}
        className="sub1_slide"
      >
        <SwiperSlide><img src={process.env.PUBLIC_URL + selproduct.imgList[0]} width="100%" /></SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL +selproduct.imgList[1]} width="100%" /></SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL +selproduct.imgList[2]} width="100%" /></SwiperSlide>
        
      </Swiper>
        
        
        <div className="pdimgbox">
            <img src={process.env.PUBLIC_URL +selproduct.sideImg[0]}/>
            <img src={process.env.PUBLIC_URL +selproduct.sideImg[1]}/>
        </div>

    </div>

    <div className="sizebox">
    <img src={process.env.PUBLIC_URL +selproduct.detailinfo} className="dinfo"/>

        <h5> </h5>
        <img src={process.env.PUBLIC_URL +"/img/size1.jpg"}/>
        
    <table className="sizetable">
        <thead>
            <tr>
                <th></th>
                <th>XS</th>
                <th>S</th>
                <th>M</th>
                <th>L</th>
                <th>XL</th>
            </tr>
            <tr>
                <th> [CHEST WIDTH]</th>
                <td>60</td>
                <td>65</td>
                <td>68</td>
                <td>72</td>
                <td>61</td>
            </tr>
            <tr>
                <th> [SLEEVE LENGTH]</th>
                <td>58</td>
                <td>59</td>
                <td>60</td>
                <td>55</td>
                <td>62</td>
            </tr>
            <tr>
                <th>  [TOTAL LENGTH]</th>
                <td>65</td>
                <td>58</td>
                <td>70</td>
                <td>71</td>
                <td>76</td>
            </tr>
            <tr>
                <th> [SHOULDER WIDTH]</th>
                <td>53</td>
                <td>56</td>
                <td>61</td>
                <td>62</td>
                <td>66</td>
            </tr>
        </thead>
    </table>
   
    <div className="sizetext">
        <h5>   1~2cm       .</h5>
        <p>           <br/>
            .</p>
        
        <h5></h5>
        <p>     ()  (02-541-0854)   <br/>
             .</p>

        <h5>  </h5>
        <p> ( )  7      . <br/>
          (   )    .</p>
    </div>
    </div>
            </Col>
            <Col md={5}>
                <div className="rbox">
                <h4 className='pt-5'>{selproduct.title}</h4>
                <div className="pricebox">
                <h5></h5>
                <p> {selproduct.price.toLocaleString()}</p>
                </div>

                <ButtonGroup>
                {
                radios.map((radio, idx) => (
                <ToggleButton
                    key={idx}
                    id={`radio-${idx}`}
                    type="radio"
                    variant={idx % 2 ? 'outline-dark' : 'outline-dark'}
                    name="radio"
                    value={radio.value}
                    checked={radioValue === radio.value}
                    onChange={(e) => setRadioValue(e.currentTarget.value)}
                >
                    {radio.name}
                </ToggleButton>
                ))}
                </ButtonGroup>
                
               
              
                <table className="order">
                    <thead>    
                    
                 
                        <tr >
                            <th>&nbsp;:&nbsp;<span>20</span></th>
                            <th >
                                <button onClick={()=>{
                                    dispatch(decrease(1))
                                 }}>-</button>
                                <p> {pronum} </p>
                                 <button onClick={()=>{
                                    dispatch(increace(1))
                                 }}
                                >+</button> 
                            </th>
                            <th>
                            {(selproduct.price*pronum).toLocaleString()}
                            
                            </th>          
                        </tr>  
 
                    </thead>  
                </table>

            
                <div className="buybtn">
                    <Button variant="dark"  onClick={()=>{
            dispatch(addItem({id:selproduct.id, item:selproduct.title, price1:selproduct.price*pronum, option:radioValue, imgUrl:selproduct.imgUrl, amount:pronum   

            }))
            navigate('/Cart')}}></Button>
                    <Button variant="light" onClick={handleShow}></Button>{' '}
                </div>
        

                <Accordion defaultActiveKey="0">
        <Accordion.Item eventKey="0">
            <Accordion.Header> </Accordion.Header>
            <Accordion.Body>
            CJ (1588-1255) <br/>
              :  (  )  <br/>
             : 10       <br/>
              :   2      <br/>
                1~3  .
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="1">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
            /      
            7        .
                 /  ,
                 .
             (         / )
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2">
            <Accordion.Header>A/S </Accordion.Header>
            <Accordion.Body>
                    <br/>
            A/S    .
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="3">
            <Accordion.Header>    </Accordion.Header>
            <Accordion.Body>
                      . <br/>
                    . <br/>
                       .
            </Accordion.Body>
        </Accordion.Item>
        </Accordion>
                </div>
            </Col>
                    
            </Row>



      <Modal show={show} onHide={handleClose} className="cartmodal">
        <Modal.Header>
          <Modal.Title>  .
        <Button variant="dark" onClick={handleClose}> </Button>
          <Button variant="warning" onClick={()=>{
            dispatch(addItem({id:selproduct.id, item:selproduct.title, price1:selproduct.price, imgUrl:selproduct.imgUrl, 
                option:radioValue, amount:pronum
            }))
            navigate('/Cart')}} >
             
          </Button>  
          </Modal.Title>
        </Modal.Header>
      </Modal>  

              
    </div>
  )
}
export default Detail
