import React, { useEffect } from 'react';
import { Button, Table, Alert } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { addCount, sortName, addCount2, price1, deleteItem } from '../store';
import { useState  } from 'react';

const Cart = () => {
    let user = useSelector(state => state.user);
    let cart = useSelector(state => state.cart);

    let dispatch = useDispatch()

    function total() {
      let sum = 0;
      for (let i in cart){
         sum += cart[i].price1 * cart[i].amount
      }
      return sum
    }


    

   return (
    <div className='cartbox'>
     <h4> </h4>
      <div className='cartbtnbox'>
         <div className='cartbtn1'onClick={()=>{
     dispatch(price1()) 
        }}></div>
     <div className='cartbtn1' onClick={()=>{
     dispatch(sortName())
        }}></div>{' '}
      </div>

      <Table style={{margin: "10px auto"}}>
         <thead>
            <tr>
               <th> 1</th>
               <th> </th>
               <th>()</th>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
            </tr>
         </thead>
         <tbody>
         { 
            cart.map((v, i) =>
            <tr key={i}>
               <td><p>{i+1}</p></td>


                <td><img src={process.env.PUBLIC_URL + v.imgUrl} /></td>
                 

                <td>
                  <p>{v.item} <br/>
                   : {v.option}</p>
                </td>

                <td>
                  <p>{v.price1.toLocaleString()}</p>
                </td>
               
                <td>
                  <div className='tdcount'>
                  <button onClick={()=>
                    dispatch(addCount2(v.id))
                }>-</button>{v.amount}
                <button onClick={()=>
                    dispatch(addCount(v.id))
                }>+</button>
                  </div>
                </td>

                <td><p>{(v.price1 * v.amount).toLocaleString()}</p></td>
                <td><button  onClick={()=>{dispatch(deleteItem(v.id))}}>X</button></td>
            </tr>
            )
            }
        </tbody>
      </Table>
      <div className='orderbox'>
         <div className='deletebtnbox'>
            <button className='deletebtn'></button>
         </div>
         <div className='ordertext'>
            <p>  </p>
            <p>{total().toLocaleString()}</p>
            
         </div>
         <Button variant="dark"></Button>
      </div>

</div>
   );
};
export default Cart;


